body {
  background-image: url("../public/photo.jpg");
  background-size: cover;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form {
  margin-left:10% ;
  margin-right: 20%;
  margin-top: 10px;
}

table {
  margin-top: 10px;
}

#poll {
  border: solid black 2px;
  box-shadow: 6px 6px;
  margin-top: 30px;
}

#poll h2 {
  text-align: center;
  padding-top: 3%;
  padding-bottom: 3%;
}

.flex {
  display: flex;
}

.img {
  width: auto;
  height: 100px;
  vertical-align: middle;
}

#vs {
  margin-left: 2%;
  margin-right: 2%;
  
}

.plants {
  margin-left: 8%;
  margin-right: 8%;
  margin-top: 2%;
}

.myCard {
  max-width: fit-content;

}

.home {
margin: auto;
text-align: center;
padding: 150px 0;

}

.home h1 {
  line-height: 2.5em;
  font-size: 5em;
}

.home h3 {
  line-height: 2.5em;
}

.homebtn {
  margin-right: 10px;
  margin-left: 10px;
  max-height: fit-content;
  text-decoration: none;
  background-color: rgb(66, 114, 68);
  padding: 7px 12px;
  color: white;
}

.plants h3 {
  padding-top: 15px;
}

.card {
  margin-top: 10px;
}

#specialCard {
  align-items: center;
}

#headline {
  text-align: center;
}

.details {
  margin-top: 8%;
}

.col-md-4 img {
  height: fit-content;
  text-align: center;
}

.boughtBy {
  list-style: none;
}

#first {
  text-decoration: underline;
}

.btn-primary {
  margin-right: 4px;
  margin-top: 4px;
  background-color: rgb(66, 114, 68);
}